.show-block {
  width: 100%;
  max-width: 350px;
  display: block;
  animation: show 0.7s forwards linear;
}
@keyframes show {
  0% {
    animation-timing-function: ease-in;
    width: 0%;
  }
  100% {
    animation-timing-function: ease-in;
    width: 100%;
  }
}
.hide-block {
  width: 100%;
  max-width: 350px;
  display: block;
  animation: hide 0.7s forwards linear;
}
@keyframes hide {
  0% {
    animation-timing-function: ease-out;
    width: 100%;
  }
  100% {
    animation-timing-function: ease-out;
    width: 0%;
    overflow: hidden;
  }
}
